<template>
    <v-dialog v-if="dialog" v-model="dialog" width="500" transition="dialog-bottom-transition" persistent>
        <v-card>
            <v-card-title>
                Añadir viaje
                <v-spacer></v-spacer>
                <slot name="close"></slot>
            </v-card-title>
            <v-card-text>
                <v-form v-model="valid">
                    <v-stepper  v-model="e1" class="elevation-0" non-linear>
                        <v-stepper-items>
                            <v-stepper-content step="1" style="padding: 0px">
                                <v-text-field
                                    filled single-line
                                    label="Punto salida" dense
                                    rounded hint="Punto de salida"
                                    :rules="[required('Punto de salida')]"
                                    v-model="data.punto_inicio" persistent-hint
                                    color="#2950c3" :disabled="false"
                                ></v-text-field>

                                <v-text-field
                                    filled single-line
                                    label="Punto llegada" dense
                                    rounded hint="Punto de llegada"
                                    :rules="[required('Punto de llegada')]"
                                    v-model="data.punto_fin" persistent-hint
                                    color="#2950c3" :disabled="false"
                                ></v-text-field>
                                
                                <v-text-field
                                    filled single-line
                                    label="Kilometros" dense
                                    rounded hint="Kilometros"
                                    :rules="[required('kilometros'),positivo('Kilometros')]"
                                    v-model="data.kilometros" persistent-hint
                                    color="#2950c3" :disabled="false"
                                    type="number"
                                ></v-text-field>
                                
                                <v-select
                                    dense filled single-line
                                    rounded label="Transporte"
                                    hint="Transporte" persistent-hint
                                    color="#2950c3" :disabled="false"
                                    :rules="[requiredObject('Transporte')]" return-object
                                    @change="changeTransporte($event)" :items="vehiculos"
                                    item-text="nombre"
                                ></v-select>

                                <v-checkbox
                                    v-model="ida"
                                    color="green"
                                    label="Ida y vuelta ?"
                                    @change="changeIda($event)"
                                ></v-checkbox>

                                <v-btn 
                                    :disabled="!valid" rounded block color="#005598" 
                                    class="white--text font-weight-bold caption"
                                    @click="e1=2"
                                >
                                    Siguiente
                                </v-btn>
                            </v-stepper-content>

                            <v-stepper-content step="2" style="padding: 0px">
                                <div class="my-5 title black--text text-center">¿ Alguien más ira contigo ?</div>

                                <v-select
                                    dense filled single-line
                                    rounded label="Usuarios"
                                    hint="Usuarios" persistent-hint
                                    color="#2950c3" :disabled="false"
                                    return-object :items="allUsers"
                                    multiple chips small-chips
                                    item-text="nombre"
                                    @change="add($event)"
                                ></v-select>

                                <v-btn 
                                    :disabled="!valid" rounded block color="#005598" 
                                    class="white--text font-weight-bold caption"
                                    @click="postViajes()" :loading="loading"
                                >
                                    Agregar
                                </v-btn>
                            </v-stepper-content>
                        </v-stepper-items>
                    </v-stepper>
                </v-form>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
import {mapState,mapActions} from 'vuex';
import mensajes from '@/mixins/mensajes';
import validations from '@/validations/validations';
import Data from '@/api/Data';

    export default {
        props:{
            dialog:{
                type:Boolean,
                default:false
            }
        },
        mixins:[mensajes],
        data(){
            return {
                ...validations,
                loading:false,
                e1:1,
                ida:false,
                valid:false,
                data:{
                    punto_inicio:"",
                    punto_fin:"",
                    kilometros:0,
                    idayvuelta:0,
                    medio_transporte_id:0,
                    created_at:new Date().toISOString().substr(0, 10),
                    updated_at:new Date().toISOString().substr(0, 10)
                },
                data1:[]
            }
        },
        mounted(){
            this.data1.push(this.user.data.id);
        },  
        computed:{
            ...mapState('sesion',{user:'user'}),
            ...mapState('global',{vehiculos:'vehiculos',usuarios:'usuarios'}),

            allUsers(){
                let users = [];
                this.usuarios.filter(a => a.id !== this.user.data.id ? users.push(a):null);
                return users;
            }
        },
        methods:{
            ...mapActions("global",['pushViaje']),

            changeIda(event){
                event ? this.data.idayvuelta = 0:this.data.idayvuelta=1;
            },
            changeTransporte(event){
                this.data.medio_transporte_id = event.id;
            },
            add(event){
                this.data1 = [];
                this.data1.push(this.user.data.id);
                event.filter(a => this.data1.push(a.id));
            },
            postViajes(){
                this.loading = true;
                Data().post("/viajes",{data:this.data,data2:this.data1}).then((response) => {
                    this.success("viaje guardado exitosamente");
                    this.data.detalles = this.data;
                    this.pushViaje(this.data);
                }).catch(e => {
                    console.log(e);
                    this.error("Error al crear el viaje");
                }).finally(() => {
                    this.loading = false;
                })
            }
        }
    }
</script>